import React, { useState } from 'react';
import { Button, IncrementDecrement } from 'components';
import { Background, ModalContainer, Label, SelectContainer, ButtonContainer } from './styled';

const Component = ({ showModal }) => {
    const [value, setValue] = useState(0)
    {
        showModal && (<Background>
            <ModalContainer>
                <Label>
                    ¿Cuántos dias quieres rentar el producto?
                </Label>
                <SelectContainer>
                    <IncrementDecrement valueSelect={value} setValueSelect={setValue} limit={value + 1} />
                </SelectContainer>
                <ButtonContainer>
                    <Button children={"Realizar renta"} onClick={() => console.log("me clickeo")} />
                </ButtonContainer>

            </ModalContainer>
        </Background>)
    }

}

export default Component;
export { Component as Modal }